<template>
    <div class="main-header">
      <h1>我们在一起</h1>
      <h2>{{parseInt((new Date().getTime() - new Date(2018, 9, 10).getTime()) / 24 / 60 / 60 / 1000)}}天</h2>
      <h2>{{message}}</h2>
      <!--<input type="text" v-model="message">-->
      <!--<button @click="counter++">click here</button>-->
      <p >{{counter}}</p>
    </div>
</template>

<script>
export default {
  data () {
    return {
      message: '啦',
      counter: ''
    }
  }
}
</script>

<style scoped>
.main-header{
  /*border: 1px solid black;*/
  text-align: center;
}
</style>



